const curEnv = 'dev';
// const curEnv = 'prod';

const serverInfo = {
  prod:'music',
  dev:'music-01',
  python:'8002',
  java:'8001'
}

/**
 * @description: 
 * @param {lan:java|python} 
 * @return: 
 */
export const getServerURL = function (lan) {
  return `https://${serverInfo[curEnv]}.niracler.com:${serverInfo[lan]}`
}
