<template>
  <div class="wrapper">
    <el-tabs v-model="activeName">
      <el-tab-pane label="" name="first">
        <!--  -->
        <!-- <div class="btn-group">
          <el-button type="primary" icon="el-icon-video-play" @click="playAll"></el-button>
          <el-button icon="el-icon-circle-plus-outline"></el-button>
          <el-button icon="el-icon-download"></el-button>
          <el-button icon="el-icon-document-copy" @click="batchProcessing($event)"></el-button>
        </div> -->
        <SelectedTable 
        :isSelection="isSelection" 
        :type="'favour'" 
        ref="child"></SelectedTable>
      </el-tab-pane>
      <el-tab-pane label="" name="second">
        <SelectedTable 
        :isSelection="isSelection" 
        :type="'playlist'"></SelectedTable>
      </el-tab-pane>
    </el-tabs>
    
  </div>
</template>

<script>
import SelectedTable from '@/components/mine/SelectedTable.vue';
export default {
  name: 'Favour',
  data(){
    return{
      activeName:'first',
      isSelection: false
    }
  },
  components:{
    SelectedTable
  },
  methods: {
    // 
    batchProcessing(e){
      if(this.isSelection){
        e.currentTarget.lastChild.innerHTML = '';
      } else {
        e.currentTarget.lastChild.innerHTML = '';
      }
      this.isSelection = !this.isSelection;
    },
    // 
    playAll(){
      this.$refs.child.playAll();
    }
  }
}

</script>
<style scoped lang='less'>
.wrapper{
  min-height: 400px;
}
</style>